.img-container {
    width: 100%;
    background-color: #04213C;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px 0;
}

body {
    margin: 0;
}

#title {
    max-height: 90px;
}

#logo {
    max-height: 100px;
}

.text {
    margin: 0 auto;
    white-space: nowrap;
    overflow: hidden;
    position: absolute;
    font-size: 20px;
    font-weight: 700;
    font-style: italic;
    width: 100vw;
    font-family: "Inria Serif", serif;
}
  
.text span {
    display: inline-block;
    padding-left: 100%;
    animation: move-text 10s linear infinite;
}
  
.text2 span {
    animation-delay: 5s;
}

@keyframes move-text {
    0% {
      transform: translate(0, 0);
    }
    100% {
      transform: translate(-100%, 0);
    }
}

